* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

@mixin theme ($tlo) {
    background: $tlo;
    &:hover {
        background: lighten($tlo, 20%);
    }
}

$map: (
    zielony: green,
    czerwony: red,
    niebieski: blue,
    zolty: yellow
    );
.container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

.container div {
    width: 50%;
    height: 50vh;
    border: 1px solid black;
}

.green {
    @include theme(map-get($map, zielony));
}

.yellow {
    @include theme(map-get($map, zolty));
}

.red {
    @include theme(map-get($map, czerwony));
}

.blue {
    @include theme(map-get($map, niebieski));
}